import VueRouter from 'vue-router'
import Vue from 'vue'
Vue.use(VueRouter)
// 引入
import Goods from '../pages/Goods.vue'//商品
//配置router
export default new VueRouter({
  routes: [
    {
      path: '/',
      name: 'Goods',
      component: Goods//首页商品
    },
    {
      path: '/Review',
      name: '/Review',
      component: () => import('../pages/Review'),//评价
      children: [
        {
          path: '/Review/',
          name: '/Review/',
          component: () => import('../pages/Review/All.vue'),//全部
        },
        {
          path: '/Review/Satisfied',
          name: '/Review/Satisfied',
          component: () => import('../pages/Review/Satisfied.vue'),//满意
        },
        {
          path: '/Review/Discontent',
          name: '/Review/Discontent',
          component: () => import('../pages/Review/Discontent.vue'),//不满意
        }
      ]
    },
    {
      path: '/Shops',
      name: 'Shops',
      component: () => import('../pages/Shops.vue'), //商家
    },
  ]
})
