<template>
  <div id="cars">
    <h3>购物车</h3>
    <div style="min-height: 150px;max-height: 200px;overflow: scroll;">
      <div v-for="item in getfoods_x"
           :key="item.id"
           id="cars_test">
        <div id="test_sons">
          <label style="max-width:150px;color:#808080">{{item.name}}</label>
          <label style="color:red;width:30px;text-align:left">{{item.price*item.num}}¥</label>
        </div>
        <div id="Add_box">
          <van-icon name="clear"
                    size="30"
                    color="#808080"
                    v-show="item.num>0"
                    @click="clicknum(-1,item.id)" />
          <span style="margin:0 15px"
                v-show="item.num>0">{{item.num}}</span>
          <van-icon name="add"
                    size="30"
                    color="#ffc300"
                    @click="clicknum(1,item.id)" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    getfoods_x () {
      return this.$store.getters.getfoods
    }
  },
  methods: {
    clicknum (num, id) {
      this.$store.commit("charnum", { num, id })
    },
  }
}
</script>

<style lang="less" scoped>
#cars {
  border-radius: 20px 20px 0 0;

  h3 {
    font-size: 14px;
    color: white;
    text-align: center;
    background-color: #ffc300;
    letter-spacing: 1em;
    border-radius: 20px 20px 0 0;
  }
  #cars_test {
    padding: 5px;
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    #test_sons {
      flex: 1;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 5px 10px;
      margin-right: 10px;
    }
    #Add_box {
      display: flex;
      align-items: center;
    }
  }
}
</style>