<template>

  <div id="index_div">
    <div id="top"
         :style="{backgroundImage:'url('+this.madata.avatar+')'}"
         @click="clickorder">
      <div id="top_sons1">
        <van-image width="70px"
                   height="70px"
                   :src="this.madata.avatar"
                   style="margin-right:15px" />
        <div>
          <p>
            <van-tag color="red"
                     text-color="#ffff">品牌</van-tag>
            {{this.madata.name}}
          </p>
          <p>{{this.madata.description}}</p>
          <p id="notice_p">
            <van-tag color="#fff"
                     text-color="orange">减</van-tag>
            <van-notice-bar left-icon="volume-o"
                            :scrollable="false"
                            background="transparent"
                            color="#ffff"
                            style="margin-left:0;height:20px;flex:1">
              <van-swipe vertical
                         class="notice-swipe"
                         :autoplay="2000"
                         :show-indicators="false">
                <van-swipe-item v-for="(item,index) in this.madata.supports"
                                :key="index">{{item}}</van-swipe-item>
              </van-swipe>
            </van-notice-bar>

          </p>
        </div>
      </div>
      <div>
        <van-notice-bar color="#ffff"
                        background="rgba(0, 0, 0, 0.8)"
                        scrollable
                        :text="this.madata.bulletin" />
      </div>
    </div>

    <div id="main_div">
      <van-tabs type="line"
                color="#FFC300"
                title-active-color="#FFC300">
        <van-tab title="商品"
                 to="/"></van-tab>
        <van-tab title="评价"
                 to="/Review"></van-tab>
        <van-tab title="商家"
                 to="/Shops"></van-tab>
      </van-tabs>
    </div>
    <transition name="slide-fade">
      <router-view></router-view>
    </transition>
    <!-- 商店资料遮罩 -->
    <van-overlay :show="ordershow">
      <div class="wrapper"
           @click="clickstop">
        <!-- 评分 -->
        <div id="rate_box">
          <h3> {{this.madata.name}}</h3>
          <van-rate v-model="this.madata.score"
                    color="#FF9A02"
                    readonly
                    gutter="15px" />
        </div>
        <!-- 优惠信息 -->
        <div>
          <van-divider contentPosition="center"
                       :style="{ color: '#ffff', borderColor: '#ffff', padding: '0 16px' }">优惠信息</van-divider>
          <p v-for="(item,index) in this.madata.supports"
             :key="index">
            <van-tag color="#7232dd"
                     plain>减</van-tag>
            {{item}}
          </p>
        </div>
        <!-- 商家公告 -->
        <div>
          <van-divider contentPosition="center"
                       :style="{ color: '#ffff', borderColor: '#ffff', padding: '0 16px' }">商家公告</van-divider>
          <p>
            {{this.madata.bulletin}}
          </p>
        </div>
      </div>

    </van-overlay>
    <!-- 购物车 -->
    <transition name="slide-fade">
      <div id="shopcar_div"
           v-show="isshow">
        <shopcar></shopcar>
      </div>
    </transition>

    <div id="car_div"
         @click="isshow=!isshow">
      <div style="color:#ccc;font-size:14px;text-align:center;padding:10px">
        <van-icon name="smile-o" />
        <p>联系商家</p>
      </div>
      <div id="pric_div">
        <van-button color="#FFC300"
                    round
                    :style="{ height: '100%' }">
          <van-icon name="shopping-cart-o"
                    color="#000000" />
        </van-button>
        <div style="color:#fff;font-size:16px"
             id="pir">
          14.5
          <p style="color:#cccc;font-size:14px">另需配送费4元</p>
        </div>
      </div>
      <van-button color="#FFC300"
                  :style="{ height: '100%' }">
        结算
      </van-button>
    </div>
  </div>

</template>

<script>
import { shop_seller } from '@/api/api'
//引入购物车界面
import shopcar from '../pages/Shopcar'
export default {
  components: {
    shopcar
  },
  created () {
    shop_seller().then(res => {
      this.madata = res.data.data
    })
  },
  data () {
    return {
      isshow: false,
      madata: {},
      ordershow: false,
      // 评分
      value: 0
    }
  },
  methods: {
    //点击显示商店
    clickorder () {
      this.ordershow = true
    },
    //隐藏商店遮罩
    clickstop () {
      this.ordershow = false
    }
  },
}
</script>

<style lang="less" scoped>
#index_div {
  height: 100%;
  display: flex;
  flex-direction: column;
  //顶部
  #top {
    color: #ffffff;
    height: 150px;
    // 垂直滚动
    .notice-swipe {
      height: 20px;
      line-height: 20px;
    }
    #notice_p {
      display: flex;
    }
    #top_sons1 {
      padding: 20px;
      display: flex;
      justify-content: start;
      align-items: center;
      background-color: rgba(0, 0, 0, 0.4);
      div:nth-child(2) {
        line-height: 25px;
      }
      .van-image {
        border-radius: 10px;
        overflow: hidden;
      }
    }
  }
  #main_div {
    display: flex;
    flex-direction: column;
  }

  // 商店资料遮罩
  .wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100%;
    color: #ffffff;
    background-color: rgba(56, 65, 72, 0.6);
    padding: 20px;
    .van-rate {
      display: flex;
      align-items: center;
      justify-content: center;
    }
    div {
      width: 100%;
      p {
        padding: 10px;
        font-size: 12px;
        line-height: 20px;
        color: #ccc;
      }
      h3 {
        text-align: center;
        margin-bottom: 10px;
      }
    }
  }
  //购物车部分
  #car_div {
    width: 100%;
    height: 50px;
    background-color: #000;
    display: flex;
    justify-content: space-between;
    position: fixed;
    bottom: 5px;

    #pric_div {
      display: flex;
      flex: 1;
      justify-content: flex-start;
      padding: 0 10px;
      border-left: 2px solid #ffffff;
      padding: 5px;
      #pir {
        padding: 5px;
      }
    }
  }
  #shopcar_div {
    width: 100%;
    background-color: white;
    position: fixed;
    bottom: 50px;
  }
  //动画
  .slide-fade-enter-active {
    transition: all 0.4s ease;
  }
  .slide-fade-leave-active {
    transition: all 0.4s ease;
  }
  .slide-fade-enter,
  .slide-fade-leave-to {
    transform: translateY(70px);
    opacity: 0;
  }
}
</style>