<template>
  <div id="goods_div">

    <!-- 侧边切换 -->
    <div id="order_left">
      <div>
        <van-sidebar v-model="activeKey"
                     @change="onChange">
          <van-sidebar-item v-for="(item, index) in goodslist_x"
                            :key="index"
                            :title=" item.name"
                            lazy-render
                            :class="{selected: curtabindex == index}" />
          <van-sidebar-item />
        </van-sidebar>
      </div>
    </div>
    <!-- 右侧 -->
    <div id="origin_right">
      <div>
        <div v-for="(item,index) in goodslist_x"
             :key="index"
             :id="index"
             v-lazy="item">
          <h3 style="font-size: 12px;color:#808080;padding-top:10px;text-align:center">{{item.name}}</h3>
          <van-card :price="obj.price"
                    :origin-price="obj.price+100"
                    :title="obj.name"
                    :thumb="obj.imgUrl"
                    v-for="(obj,index) in item.foods"
                    :key="index"
                    @click-thumb="goodsclick(obj)">
            <template #tags
                      style="width:100%">
              <!-- 加减 -->
              <div id="Add_box">
                <van-icon name="clear"
                          size="30"
                          color="#808080"
                          v-show="obj.num>0"
                          @click="clicknum(-1,obj.id)" />
                <span style="margin:0 15px"
                      v-show="obj.num>0">{{obj.num}}</span>
                <van-icon name="add"
                          size="30"
                          color="#ffc300"
                          @click="clicknum(1,obj.id)" />
              </div>

            </template>
            <template #price-top>
              <van-tag plain
                       type="danger">特惠</van-tag>
            </template>
          </van-card>
        </div>
        <van-card></van-card>
      </div>
    </div>

    <!-- 商品详情 -->
    <van-popup v-model="show"
               closeable
               position="left"
               close-icon="close"
               close-icon-position="top-left"
               :style="{ height: '100%' }">
      <div style="width:100%;background-color:#f1f1f1">
        <img :src="newarr.imgUrl"
             style="width:100%">
        <div class="char_main">
          <h2>{{newarr.name}}</h2>
          <p><label>月售{{newarr.sellCount}}份</label><label>好评率{{newarr.rating}}%</label></p>
          <p id="char_p">
            <span style="color:red">¥{{newarr.price}}</span>
            <van-button round
                        type="info"
                        style="width:120px;font-size:14px;border:none"
                        @click="addcar(newarr.name)">加入购物车</van-button>
          </p>
        </div>

        <div class="char_main">
          <h3>商品介绍</h3>
          <p style="font-size:12px">
            {{newarr.goodsDesc}}
          </p>
        </div>

        <div id="char_main3">
          <van-tabs type="card"
                    color="#FFC300">
            <van-tab title="全部">
              <div style="overflow: scroll;background-color: white;">
                <p>11111</p>
              </div>
            </van-tab>
            <van-tab title="推荐">
              <div style="overflow: scroll;background-color: white;">
                <p>11111</p>
              </div>
            </van-tab>
            <van-tab title="吐槽">
              <div style="overflow: scroll;background-color: white;">
              </div>
            </van-tab>
          </van-tabs>

        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import { goods_list } from '@/api/api'
import BScroll from "better-scroll";
export default {
  async created () {
    let res = await goods_list();
    this.$store.commit("chargoodslist", res.data.goodsList)
  },

  mounted () {
    // 左边
    new BScroll(document.querySelector('#order_left'), {
      click: true,//允许点击事件
    })
    //右边
    this.BScroll_originRight = new BScroll(document.querySelector('#origin_right'), {
      click: true,
      probeType: 3//实时滚动
    })
    this.BScroll_originRight.on("scroll", obj => {
      let _y = Math.abs(obj.y);
      for (let obj of this.getarr) {
        if (_y >= obj.startY && _y < obj.endY) {
          this.curtabindex = obj.index
          break
        }
      }
    });
  },
  computed: {
    getarr () {
      let arr = [];
      let total = 0;
      for (let item = 0; item < this.goodslist_x.length; item++) {
        let curHeight = document.getElementById(item).offsetHeight;
        arr.push({ startY: total, endY: total + curHeight, index: item });
        total += curHeight;
      }
      return arr;
    },
    //从vuex取值
    goodslist_x () {
      return this.$store.state.goodslist
    }
  },
  data () {
    return {
      newarr: { category: "", ctime: "", goodsDesc: "", id: "", imgUrl: "", name: "", price: "", rating: "", ratings: "", sellCount: "" },
      show: false,
      //侧边导航
      activeKey: 0,
      curtabindex: 0,//选中的
    }
  },
  methods: {
    //左边侧栏点击
    onChange (index) {
      this.curtabindex = index;
      this.BScroll_originRight.scrollToElement(document.getElementById(index), 300);
    },
    clicknum (num, id) {
      this.$store.commit("charnum", { num, id })
    },
    goodsclick (vallis) {
      this.newarr = vallis
      this.show = true
    },
    //商品详情添加到购物车
    addcar (val) {
      console.log(val);
    }
  }
}
</script>

<style lang="less" scoped>
#goods_div {
  flex: 1;
  display: flex;
  overflow: hidden;
  //商品详情
  .van-popup {
    display: flex;
    flex-direction: column;
    .char_main {
      margin-bottom: 15px;
      background-color: white;
      padding: 10px;
      h2 {
        margin-bottom: 15px;
      }
      label {
        margin: 10px;
      }
      #char_p {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 10px 5px;
      }
      h3 {
        margin: 15px auto;
      }
    }
    #char_main3 {
      flex: 1;
      overflow: hidden;
      background-color: white;
      padding: 10px;
    }
  }

  button {
    background-color: #ffc300;
    width: 30px;
    height: 30px;
    font-size: 20px;
  }
  #order_left {
    width: 80px;
    height: 100%;
    overflow: scroll;
    .van-sidebar-item--select::before {
      background-color: transparent;
    }
    .selected {
      background-color: #f1f1f1;
      border-left: 2px solid red;
      box-sizing: border-box;
    }
    .van-sidebar-item {
      font-size: 12px;
    }
  }
  #origin_right {
    flex: 1;
    overflow: scroll;
    // 商品模板
    .van-card {
      margin-top: 0;
    }
    #Add_box {
      display: flex;
      align-items: center;
    }
  }
}
</style>