import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
// 使用
export default new Vuex.Store({
  state: {
    goodslist: [],
  },
  //改变数据
  mutations: {
    //函数  newhei为调用页面传回的参数
    chargoodslist (state, parsem) {
      for (const item of parsem) {
        for (const obj of item.foods) {
          obj.num = 0
        }
      }
      state.goodslist = parsem
    },
    // 点击改变数量
    charnum (state, val) {
      for (const item of state.goodslist) {
        for (const obj of item.foods) {
          if (obj.id === val.id) {
            obj.num += val.num
          }
        }
      }
      return
    }
  },
  getters: {
    // 筛选num>0
    getfoods (state) {
      let arrs = []
      for (const item of state.goodslist) {
        for (const obj of item.foods) {
          if (obj.num > 0) {
            arrs.push(obj)
          }
        }
      }
      return arrs
    }
  }
})
