import Vue from 'vue'
import App from './App.vue'
//UI组件
import Vant from 'vant';
import { Lazyload } from 'vant';
import 'vant/lib/index.css';
//router
import router from '@/router/router'
import store from './store/store'

Vue.use(Vant);
Vue.config.productionTip = false
Vue.use(Lazyload, {
  lazyComponent: true,
});
new Vue({
  store,
  router,
  render: h => h(App),
}).$mount('#app')
